import { connect } from 'react-redux';
import { withRouter } from 'react-router';
import { submit } from 'redux-form';
import moment from 'moment';

import { getOrder } from '../../selectors';
import Order from '../../components/panels/Order';
import actions from '../../actions';

const mapStateToProps = (state, { history }) => {
  const {
    doctor,
    slot,
    date,
    clinicId,
    phone,
    requestId,
    needsConfirmation,
    isLoading,
    success
  } = getOrder(state);
  const clinic = doctor.Clinics.find(({ ClinicId }) => ClinicId === clinicId);
  return {
    goHome: () => history.push('/'),
    doctor,
    clinicId,
    dateFormat: slot
      ? moment(slot.StartTime)
          .locale('ru')
          .format('D MMMM в H:mm')
      : moment(date)
          .locale('ru')
          .format('D MMMM'),
    cancel: () => history.goBack(),
    price: clinic.SpecialPrice || clinic.Price,
    phone,
    requestId,
    needsConfirmation,
    isLoading,
    success,
    date,
    slot
  };
};

const mapDispatchToProps = dispatch => ({
  onPreOrderClick: () => dispatch(submit('order')),
  onConfirmClick: () => dispatch(submit('confirmation')),
  dispatch
});

const mergeProps = (stateProps, dispatchProps, ownProps) => {
  const {
    doctor: { Id },
    clinicId,
    slot,
    date,
    phone,
    requestId
  } = stateProps;
  const { dispatch } = dispatchProps;
  return {
    ...stateProps,
    ...ownProps,
    ...dispatchProps,
    onPreOrderSubmit: params => {
      const { firstName, lastName, phoneNumber } = params;
      dispatch({
        type: actions.PLACE_PRE_ORDER,
        payload: {
          name: [firstName, lastName].join(' '),
          phone: phoneNumber.replace(/[\\+\s-\\(\\)]/g, ''),
          doctor: Id,
          clinic: clinicId,
          slotId: slot && slot.Id,
          dateAdmission: slot ? slot.StartTime : date,
          validate: 1
        }
      });
    },
    onConfirmSubmit: params => {
      dispatch({
        type: actions.CONFIRM_ORDER,
        payload: {
          ...params,
          requestId,
          phone
        }
      });
    }
  };
};

export default withRouter(
  connect(
    mapStateToProps,
    mapDispatchToProps,
    mergeProps
  )(Order)
);
