import React from 'react';
import PropTypes from 'prop-types';
import { reduxForm, Field } from 'redux-form';
import { FormLayout, FormLayoutGroup } from '@vkontakte/vkui';
import SmsCodeField from '../atoms/SmsCodeField';

const ConfirmationForm = ({ disabled, handleSubmit }) => (
  <FormLayout onSubmit={handleSubmit}>
    <FormLayoutGroup top="Введите код из SMS">
      <Field
        name="validationCode"
        component={SmsCodeField}
        disabled={disabled}
      />
    </FormLayoutGroup>
  </FormLayout>
);

ConfirmationForm.defaultProps = {
  disabled: false
};

ConfirmationForm.propTypes = {
  disabled: PropTypes.bool,
  handleSubmit: PropTypes.func.isRequired
};

export default reduxForm({
  form: 'confirmation'
})(ConfirmationForm);
